import React, { useState } from "react";

const Form = ({ handleSubmit, history }) => {
  const [searchEntry, setSearchEntry] = useState("");
  const updateSearchInput = e => {
    e.preventDefault();
    setSearchEntry(e.target.value);
  };

  return (
    <>
      <form
        className="search-form"
        onSubmit={e => handleSubmit(e, history, searchEntry)}
        onClick={e => handleSubmit(e, history, searchEntry)}
        // onMouseOut={e => handleSubmit(e, history, searchEntry)}
        // onMouseEnter={e => handleSubmit(e, history, searchEntry)}
        onFocusIn={e => handleSubmit(e, history, searchEntry)}
        onFocusOut={e => handleSubmit(e, history, searchEntry)}
      >
        <input type="text" name="search"
          onChange={updateSearchInput} placeholder="Voer hier uw zoekwoord in"
          value={searchEntry}
        />
        <button type="submit"></button>
      </form>
    </>
  );
};

export default Form;
